import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Axios from "axios";
import SelectSearch from "react-select-search";
import Swal from "sweetalert2";
import {
  getListCloseTransaksiPenjualan,
  getListCloseTransaksiPenjualanSelected,
} from "../actions/closeTransaksiPenjualanAciton";
import { gagal } from "../Alert";
import { getUUID } from "../actions/fakturAction";
import { finishSend, progressSend } from "../actions/LogicAction";
import { renderField, renderFieldSelect } from "../renderField";
import HeaderCard from "../HeaderCard";

const maptoState = (state) => {
  return {
    listValidasiCustomer: state.provinsi.listValidasiCustomer,
    listCloseTransaksiPenjualan: state.provinsi.listCloseTransaksiPenjualan,
    listCloseTransaksiPenjualanSelected:
      state.provinsi.listCloseTransaksiPenjualanSelected,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class CloseTransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
      listCloseTransaksiPenjualan: [],
    };
  }

  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getListCloseTransaksiPenjualanSelected());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };

  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
    this.getListClose();
  }
  getListClose() {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "OPEN",
      },
    }).then((res) =>
      this.setState({
        listCloseTransaksiPenjualan: res.data,
      })
    );
  }
  onSubmitToko(data) {
    this.props.change("namaToko", data);
  }
  kirimvalidasi() {
    this.props.dispatch(progressSend());
    var hasil =
      JSON.parse(localStorage.getItem("closeTransaksiPenjualan")) || [];
    Axios.put(serverNonMaster + "cst/dc/pjl", hasil)
      .then(() =>
        Swal.fire({
          text: "Cancel Transaksi berhasil",
          icon: "success",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
        })
          .then(() => localStorage.removeItem("closeTransaksiPenjualan"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListCloseTransaksiPenjualan()))
          .then(() => this.prevStep())
          .then(() => this.getListClose())
          .then(() => this.props.dispatch(finishSend()))
      )
      .catch(() =>
        gagal(
          "Cancel gagal, Mohon periksa Koneksi anda",
          this.props.dispatch(finishSend())
        )
      );
  }
  getListPenjualan(tanggalFrom, tanggalTo, kodeCustomer) {
    this.props.dispatch(
      getListCloseTransaksiPenjualan(tanggalFrom, tanggalTo, kodeCustomer)
    );
  }
  setToko(data) {
    this.props.change("namaToko", data);
    this.setFilterCustomer(data);
    this.setState({
      namaToko: data,
    });
  }
  setFilterCustomer(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "OPEN",
      },
    })
      .then((res) =>
        this.setState({
          listCloseTransaksiPenjualan: res.data,
        })
      )
      .then(() => {
        var array = this.state.listCloseTransaksiPenjualan;
        var filtered = array.filter(
          (list) => list.kodeAktor === data.toUpperCase()
        );
        this.setState({
          listCloseTransaksiPenjualan: filtered,
        });
      });
  }
  setFilterTanggal(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        statusValid: "OPEN",
      },
    })
      .then((res) =>
        this.setState({
          listCloseTransaksiPenjualan: res.data,
        })
      )
      .then(() => {
        var array = this.state.listCloseTransaksiPenjualan;
        var filtered = array.filter((list) => list.tanggal === data);
        this.setState({
          listCloseTransaksiPenjualan: filtered,
        });
      });
  }
  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.kodeCustomer,
        name: obj.namaToko,
      };
      return payload;
    });
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        const data = {
          tanggal: row.tanggal,
          noBon: row.noBon,
          supplier: row.supplier,
          bruto: row.bruto,
          netto: row.netto,
          bonRp: row.bonRp,
          titipRp: row.titipRp,
          titipGr: row.titipGr,
          keterangan: row.keterangan,
          noFaktur: row.noFaktur,
          footerDocument: {
            editBy: "Nagatech",
            statusValid: "CANCEL",
          },
        };
        if (isSelect) {
          array.push(data);
          localStorage.setItem(
            "closeTransaksiPenjualan",
            JSON.stringify(array)
          );
        } else {
          var index = array.findIndex((item, i) => {
            return item.noFaktur === row.noFaktur;
          });
          array.splice(index, 1);
          localStorage.setItem(
            "closeTransaksiPenjualan",
            JSON.stringify(array)
          );
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        rows.forEach(function (list) {
          var data = {
            tanggal: list.tanggal,
            noBon: list.noBon,
            supplier: list.namaAktor,
            bruto: list.bruto,
            netto: list.netto,
            bonRp: "",
            titipRp: "",
            titipGr: "",
            keterangan: list.keterangan,
            noFaktur: list.noFaktur,
            footerDocument: {
              editBy: "Nagatech",
              statusValid: "VALID",
            },
          };
          array.push(data);
        });
        if (isSelect) {
          localStorage.setItem(
            "closeTransaksiPenjualan",
            JSON.stringify(array)
          );
        } else {
          localStorage.removeItem("closeTransaksiPenjualan");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Close Transaksi Penjualan" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12 mb-3">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={namaToko}
                        search
                        placeholder="SILAHKAN PILIH"
                        onChange={(data) => this.setToko(data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="namaToko"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                        onChange={(e) => {
                          this.setState(
                            {
                              tanggal: e.target.value,
                            },
                            this.setFilterTanggal(e.target.value)
                          );
                        }}
                      />
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="TipeTransaksi"
                        label="Tipe Transaksi"
                        component={renderFieldSelect}
                        className="form-control"
                        onChange={(e) =>
                          this.setState({
                            TipeTransaksi: e,
                          })
                        }
                        options={[
                          { value: "UMUM", name: "UMUM" },
                          { value: "FICO", name: "FICO" },
                        ]}
                      >
                        <option
                          value="
                            DEFAULT"
                          key="
                            DEFAULT"
                        >
                          Silahkan PIlih
                        </option>
                        <option value="UMUM" key="UMUM">
                          UMUM
                        </option>
                        <option value="FICO" key="FICO">
                          FICO
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() =>
                            this.getListPenjualan(
                              this.state.tanggal,
                              this.state.tanggal,
                              this.state.kodeCustomer
                            )
                          }
                        >
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.state.listCloseTransaksiPenjualan ? (
                        <ToolkitProvider
                          keyField="noFaktur"
                          data={this.state.listCloseTransaksiPenjualan}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                selectRow={selectRow}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.listCloseTransaksiPenjualanSelected ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listCloseTransaksiPenjualanSelected}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="id"
                          data={[]}
                          columns={this.state.columns}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.prevStep()}
                            >
                              <i className="fa fa-chevron-left mr-3"></i>
                              Kembali
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.kirimvalidasi()}
                            >
                              <i className="fa fa-times-circle mr-3"></i>
                              Batal Transaksi
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

CloseTransaksiPenjualan = reduxForm({
  form: "CloseTransaksiPenjualan",
  enableReinitialize: true,
})(CloseTransaksiPenjualan);
export default connect(maptoState, null)(CloseTransaksiPenjualan);
